}

h1,h2,h3,h4 {
  color: #005475;
}

.section-heading {
  display: block;
  margin: 2rem 0;
  color: #005475;
  font-size: 1.6rem;
  font-weight: 500;
  text-align: center;
  overflow-x: hidden;
}

.section-heading span {
  display: inline-block;
  position: relative;
  padding: 0 .6rem;
}

.section-heading span::before,.section-heading span::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  height: 1px;
  width: 100%;
  background-color: #005475;
}

.section-heading span::before {
  left: -100%;
}

.section-heading span::after {
  right: -100%;
}

.subheading {
  margin-bottom: .5rem;
  font-size: 1.5rem;
  font-weight: 700;
}

.subheading--light {
  color: #fff;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*,*:before,*:after {
  box-sizing: border-box;
}

body,h1,h2,h3,h4,h5,h6,p,ol,ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol,ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

.is-blocked {
  overflow: hidden;
}

.nav {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  z-index: 200;
  background-color: #fff;
}

.nav--scrolled .nav-toggler {
  top: 13px;
}

.nav--scrolled .nav__logo {
  width: 60px;
}

.nav__container {
  padding: 1rem;
}

.nav__brand {
  transition: opacity .3s ease;
}

.nav__brand:hover {
  opacity: .8;
}

.nav__logo {
  width: 80px;
  transition: width .3s ease;
}

.nav__menu {
  position: fixed;
  top: -100vh;
  left: 0;
  margin-top: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-left: auto;
  background-color: rgba(66, 66, 66, 0.9);
  opacity: 0;
  transition: top .6s, opacity .6s;
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
}

.nav__menu.collapsed {
  top: 0;
  opacity: 1;
}

.nav__item {
  width: 100%;
}

.nav__link {
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  perspective: 1000;
}

.nav__link:link,.nav__link:visited {
  display: block;
  padding: 1rem 0;
  color: #fff;
  font-family: "Montserrat", sans-serif;
  font-size: 1.2rem;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
  transition: background-color .3s ease, color .3s ease;
}

.nav__link:active,.nav__link:hover {
  background-color: #fff;
  color: #424242;
}

.nav-toggler {
  display: block;
  z-index: 1030;
  position: fixed;
  top: 20px;
  right: 20px;
  width: 40px;
  height: 40px;
  cursor: pointer;
  transition: top .3s ease;
}

.nav-toggler span {
  display: block;
  position: absolute;
  top: 18px;
  width: 100%;
  height: 3px;
  background-color: #AF1D22;
  transition: transform .2s ease;
}

.nav-toggler span::before,.nav-toggler span::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 3px;
  background-color: #AF1D22;
  transition: top .2s ease, transform .2s ease;
}

.nav-toggler span::before {
  top: -10px;
}

.nav-toggler span::after {
  bottom: -10px;
}

.nav-toggler.is-open span {
  transform: rotate(45deg);
}

.nav-toggler.is-open span::before {
  opacity: 0;
}

.nav-toggler.is-open span::after {
  top: 0;
  transform: rotate(-90deg);
