.swiper-container {
  width: 100%;
  height: 100vh;
  max-height: 882px;
}

.swiper-slide {
  background: no-repeat top center / cover;
}

.swiper-slide--1 {
  background-image: linear-gradient(rgba(0, 84, 117, 0.5), rgba(0, 84, 117, 0.5)), url("../../assets/images/slider-1.jpg");
}

.swiper-slide--2 {
  background-image: linear-gradient(rgba(0, 84, 117, 0.5), rgba(0, 84, 117, 0.5)), url("../../assets/images/slider-2.jpg");
}

.swiper-slide--3 {
  background-image: linear-gradient(rgba(0, 84, 117, 0.5), rgba(0, 84, 117, 0.5)), url("../../assets/images/slider-3.jpg");
}

.swiper-slide--4 {
  background-image: linear-gradient(rgba(0, 84, 117, 0.5), rgba(0, 84, 117, 0.5)), url("../../assets/images/slider-4.jpg");
}

.swiper-slide--5 {
  background-image: linear-gradient(rgba(0, 84, 117, 0.5), rgba(0, 84, 117, 0.5)), url("../../assets/images/slider-6.jpg");
}

.swiper-button-prev,.swiper-button-next {
  top: auto;
  bottom: 5px;
  transition: transform .3s ease;
}

.swiper-button-prev:hover,.swiper-button-next:hover {
  transform: scale(1.15);
}

.swiper-button-prev {
  left: 30%;
  background-image: url("../../assets/images/icon-arrow-left.svg");
}

.swiper-button-next {
  right: 30%;
  background-image: url("../../assets/images/icon-arrow-right.svg");
}

.swiper-pagination-bullet-active {
  background: #fff;
}

.swiper-pagination-fraction,.swiper-pagination-custom,.swiper-container-horizontal > .swiper-pagination-bullets {
  bottom: 22px;
}

.btn {
  position: relative;
  display: inline-block;
  padding: .8rem 1.8rem;
  border: solid 1px transparent;
  box-shadow: 0 0 1px #999;
  font-size: 1rem;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  color: #fff;
  transition: transform .25s ease, box-shadow .25s ease;
  cursor: pointer;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  perspective: 1000;
}

.btn:hover {
  opacity: .8;
  box-shadow: 0 1px 2px #999;
}

.btn--primary {
  background-color: #005475;
  transition-property: box-shadow, opacity;
}

.btn--primary::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  width: 15px;
  height: 15px;
  background: url("../../assets/images/icon-arrow-right.svg") no-repeat center/contain;
}

.slogan {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.slogan__title {
  padding: .7rem;
  margin: 0 2.5rem;
  border: 1px solid rgba(255, 255, 255, 0.7);
  color: #fff;
  font-size: 1.8rem;
  font-weight: 300;
  text-align: center;
  text-transform: uppercase;
  cursor: default;
}

.slogan__title--small {
  font-size: 2.3rem;
}

.slogan__title--description {
  display: block;
  font-size: 1.3rem;
  text-align: center;
}

.swiper-slide .slogan {
  opacity: 0;
  transform: translateY(20px);
  transition: transform .5s linear .1s, opacity .5s linear .1s;
}

.swiper-slide-active .slogan {
  opacity: 1;
  transform: translateY(0);
}

.checklist {
  padding: 2rem;
}

.checklist--reverse {
  background-color: #005475;
}

.checklist__heading {
  margin-bottom: .5rem;
  font-size: 1.5rem;
  font-weight: 700;
  color: #fff;
}

.checklist__heading--small {
  margin-top: 1rem;
  font-size: 1rem;
}

.checklist__heading--reverse {
  display: inline-block;
  padding: .2rem .3rem;
  color: #005475;
  background-color: rgba(255, 255, 255, 0.9);
}

.checklist__list-item {
  position: relative;
  padding-left: 30px;
  color: #fff;
}

.checklist__list-item::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  background: url("../../assets/images/icon-check-reverse.svg") no-repeat center/contain;
}

.register {
  padding: 2rem;
  border: 1px solid #005475;
}

.register__btn {
  margin-top: 2rem;
  width: 100%;
}

.recruitment {
  padding: 1.5rem;
  background-color: #005475;
  color: #fff;
}

.text-box {
  padding: 1.5rem;
  border: 1px solid #005475;
}

#map {
  width: 100%;
  height: 500px;
}

.reason__box {
  display: block;
  position: relative;
  flex: 1;
  padding: 3rem 2rem;
  border-bottom: 1px solid #005475;
  background-color: #005475;
  color: #fff;
  text-align: center;
  transition: all .3s ease;
  overflow-y: hidden;
  cursor: pointer;
}

.reason__box:hover {
  opacity: .9;
}

.reason__box:hover::before {
  top: 8px;
}

.reason__box:hover .reason__title {
  transform: translateY(-5px);
}

.reason__box:hover .reason__description {
  transform: translateY(3px);
  color: #fff;
}

.reason__box--lighten {
  opacity: .8;
  background-color: #005475;
}

.reason__box--lighten:hover {
  opacity: .9;
  background-color: #005475;
  color: #fff;
}

.reason__box::before {
  content: "";
  display: block;
  position: absolute;
  content: "";
  display: block;
  position: absolute;
  width: 0;
  height: 0;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
  border-top: 20px solid #fff;
  margin-top: -8px;
  top: -15px;
  left: 50%;
  transform: translateX(-50%);
  transition: top .3s ease;
}

.reason__title,.reason__description {
  color: #fff;
}

.reason__title {
  font-size: 1.25rem;
  text-transform: uppercase;
  transition: transform .3s ease;
}

.reason__description {
  margin-top: .6rem;
  color: #e6e6e6;
  font-size: .9rem;
  line-height: 1.45;
  transition: transform .3s ease, color .3s ease;
}

.seekers {
  padding: 3rem 0;
}

.seekers__inner {
  margin-top: 3rem;
}

.seekers__description {
  color: #686868;
  text-align: center;
  font-weight: 700;
}

.seekers__checklist {
  flex: 1;
}

.seekers__register {
  flex: 1;
}

.employers {
  position: relative;
  padding: 5rem 0;
}

.employers__checklist {
  padding: 0;
}

body {
  min-height: 200vh;
}

@media (min-width: 768px) {
  .section-heading {
    font-size: 2.2rem;
  }

  .section-heading span {
    padding: 0 1rem;
  }

  .container {
    max-width: 740px;
  }

  .nav--scrolled .nav__logo {
    width: 75px;
  }

  .nav__container {
    display: flex;
    align-items: center;
    padding: 1rem 0;
  }

  .nav__logo {
    width: 100px;
  }

  .nav__menu {
    position: static;
    flex-direction: row;
    justify-content: flex-end;
    width: auto;
    height: auto;
    opacity: 1;
    background: transparent;
  }

  .nav__item {
    margin: .2rem .4rem;
    width: auto;
  }

  .nav__link:link,.nav__link:visited {
    display: inline-block;
    position: relative;
    padding: .4rem 0;
    color: #424242;
    font-size: .8rem;
    font-weight: 500;
  }

  .nav__link:link::before,.nav__link:link::after,.nav__link:visited::before,.nav__link:visited::after {
    content: "";
    display: block;
    position: absolute;
    width: 0;
    height: 1px;
    background-color: #AF1D22;
    transition: width .3s ease;
  }

  .nav__link:link::before,.nav__link:visited::before {
    top: 0;
    left: 0;
  }

  .nav__link:link::after,.nav__link:visited::after {
    bottom: 0;
    right: 0;
  }

  .nav__link:active,.nav__link:hover {
    color: #888;
  }

  .nav__link:active::before,.nav__link:active::after,.nav__link:hover::before,.nav__link:hover::after {
    width: 100%;
  }

  .nav-toggler {
    display: none;
  }

  .swiper-container {
    height: 550px;
  }

  .swiper-button-prev,.swiper-button-next {
    top: 50%;
  }

  .swiper-button-prev {
    left: 10px;
  }

  .swiper-button-next {
    right: 10px;
  }

  .btn--primary::after {
    right: 20px;
    width: 20px;
    height: 20px;
    transition: right .3s ease;
  }

  .btn--primary:hover::after {
    right: 10px;
  }

  .recruitment {
    padding: 2rem;
  }

  .text-box {
    padding: 2rem;
  }

  .reason__container {
    display: flex;
  }

  .reason__title {
    font-size: 1.5rem;
  }

  .seekers__inner {
    display: flex;
    margin-top: 4rem;
  }

  .employers__hiring,.employers__recruitment {
    flex: 1;
  }

  .employers__hiring {
    margin-right: 2rem;
  }
}

@media (min-width: 992px) {
  html {
    font-size: 100%;
  }

  .container {
    max-width: 960px;
  }

  .swiper-container {
    height: 700px;
  }

  .slogan__title {
    font-size: 2.7rem;
  }

  .seekers {
    padding: 6rem 0;
  }

  .seekers__checklist {
    margin-right: 2rem;
  }

  .employers__inner {
    display: flex;
  }
}

@media (min-width: 1230px) {
  .container {
    max-width: 1200px;
  }
}

@-webkit-keyframes swiper-preloader-spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes swiper-preloader-spin {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
